
.projects{
    width:100%;
    height: 200vh;
    background: linear-gradient(hsla(60, 100%, 97%, 1) 0%, hsla(0, 0%, 0%, 1) 100%);
    background-repeat: no-repeat;
    background-size: 100%;
  }

  .projectDescription{
    width:100%;
    text-align:left;
    display: flex;
    margin-top:5%;
  }
  .projectsTitle{
    text-align: center;
    margin:0;
    margin-left:5vw;
    width:100%;
    font-family: "Consolas";
    font-size: 20px;
    font-weight:600;
    color:midnightblue;
  }
  .projectsText{
    width:100%;
    position:relative;
    word-wrap: break-word;
    text-align: left;
    margin-left: 5vw;
    padding:0px;
    margin-top:0;
    font-family: "Consolas";
    font-size:16px;
    color:midnightblue;
  }
  .projectsPic{
    display:relative;
    margin-left:5%;
    height: 20vh;
    margin-right:5%;
    border:5px solid rgb(166, 191, 223);
  }

  .projectsContainer{
    margin-left: 10vw;
    margin-right: 15vw;
    padding-left: 10%;
    padding-right: 10%;
    display:flex;
    height:20vh;
  }
  .left1 {
    position:relative;
    height:80vh;
    width:50%;
    float:left;
    left:0;
  }
  
  /* Control the right side */
  .right1 {
    position:relative;
    height:80vh;
    width: 50%;
    float:right;
    right:0;
    margin-top: 0%;
  }