.about{
    width:100%;
    height:100vh;
    background-color:#fffff0;
    background-repeat: no-repeat;
    background-size: cover;
  }
.header{
  font-family: 'Consolas' ;
  color: midnightblue;
  font-weight:normal;
  margin-top: 3%;
}
  /* Control the left side */
  .left {
    position:relative;
    height:80vh;
    width:60%;
    float:left;
    left:0;
  }
  
  /* Control the right side */
  .right {
    position:relative;
    height:80vh;
    width: 40%;
    float:right;
    right:0;
    margin-top: 0%;
  }
  
.body{
    width:100%;
    position: relative;
    text-align: left;
    margin-top:0%;
  }
.body .img {
    position: relative;
    margin-left:25%;
    margin-top: 5%;
    width:50%;
    box-shadow: rgba(0,0,0,0.3);
    overflow: hidden;
}
.body .text {
    position: flex;
    width: 90%;
    height:100%;
    margin-left: 10%;
    font-size:medium;
    font-family: "Consolas";
    color: midnightblue;
}

.container{
  width: 100%;
  margin-top: 1%;
  margin-left:5%;
  position:relative;
}

*{
  font-family:sans-serif;
  list-style: none;
  padding:0;
}
.skills{
  width:100%;
  position: relative;
  color:white;
}
.skills li{
  margin-left:10%;
  margin-right:10%;
  margin-top: 2%;
}
.bar{
  background:transparent;
  display:block;
  height:7px;
  overflow: hidden;
  box-shadow: 0 0 10px #A6BFDFb3;
}
.bar span{
  height:7px;
  float: left;
  background:rgb(166, 191, 223);
}
.htmlcss{
  width: 80%;
  animation: html 3s;
}
@keyframes html{
  0%{ 
    width:0%;
  }
  100%{
    width:80%;
  }
}
.python{
  width: 90%;
  animation: python 3s;
}
@keyframes python{
  0%{ 
    width:0%;
  }
  100%{
    width:90%;
  }
}
.nppd{
  width:85%;
  animation: nppd 3s;
}
@keyframes nppd{
  0%{ 
    width:0%;
  }
  100%{
    width:85%;
  }
}
.c{
  width:60%;
  animation: c 3s;
}
@keyframes c{
  0%{ 
    width:0%;
  }
  100%{
    width:60%;
  }
}
.cons{
  width:70%;
  animation: cons 3s;
}
@keyframes cons{
  0%{ 
    width:0%;
  }
  100%{
    width:70%;
  }
}