.homePage {
    width:100%;
    height:100vh;
    background: url(blue-triangle-overlay-1-light.png) no-repeat center;
    background-size: cover;
    background-color: #FFFFF0;
    display: flex; 
    flex-direction: column;
}

  .socials{
    margin-top: 5vh;
    display:inline;
    padding-inline-end: 1.75%;
    padding-inline-start: 1.75%;
    height: 5vh;
  }
  .socials a{
    height: 20vw;
  }
  
  
  @keyframes typing {
    from{
    width: 0;
    color: midnightblue;
    }
    to {
    text-shadow:rgb(166, 191, 223);
    transition-duration:0s;
    color: midnightblue ;
    }
}
  @keyframes blink {
    50% {
      border-color: white;
    }
  }
  
  @keyframes typing1{
    from {
      color:midnightblue;
      width: 0;
    }
    to {
      transition-duration: 0s;
      color:midnightblue ;
    }
  }

 .homeName {
    width:50%;
    margin-left:25%;
    margin-right:25%;
    margin-top:20vh;
    margin-bottom: 0px;
    font-size: 8.2vw;
    font-family: 'Consolas', 'Courier';
    font-weight:1024px;
    color:transparent;
    text-shadow: 0 0.5vh rgb(166, 191, 223); 
    border-right: 10px white;
    overflow: hidden;
    white-space: nowrap;
    animation: typing 1.1s steps(11, end), blink 0.2s step-end;
    animation-fill-mode: forwards;
  }

  .homeSubtitle{
    width:40%;
    margin-left: 30%;
    margin-right: 30%;
    font-size: 1.2vw;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color:transparent;
    border-right: 10px white;
    white-space: nowrap;
    overflow:hidden;
    animation: typing1 2s steps(65, end), blink 0.2s step-end;
    animation-delay:1s;
    animation-fill-mode: forwards;
  }

