.App {
  text-align: center;
}

html{
  scroll-behavior: smooth;
}

.nav {
  z-index: 9999;
  top:0;
  left: 0;
  align-items: end;
  width: 100%;
  position: fixed;
  background:transparent;
}
.headerText{
  font-size: 14px;
  display: inline;
  padding:2% ;
  color:midnightblue;
} 
.nav a {
  float: center;
  color: midnightblue;
  text-align: center;
  padding: 0.911vw 0.95vw;
  text-decoration: none;
  font-size: 0.911vw;
  font-family:'Open Sans', 'Helvetica Neue', sans-serif;
  letter-spacing: 1%;
}
.nav :hover {
  color: rgb(166, 191, 223);
}
.nav .active{
  color:rgb(166, 191, 223);
}
.misc{
  width:100%;
  height: 49vw;
  background-image:url(/src/coolBackground.png);
  background-repeat: no-repeat;
  background-size: cover;
}
.description{
  font-family: "Playfair Display";
  color:midnightblue;
}
.section{
  display:block;
  padding-top: 4%;
  padding-bottom: 0%;
  margin-top: 0%;
  margin-bottom: 0%;
  font-family:'Open Sans', 'Helvetica Neue', sans-serif;
  color:rgb(166, 191, 223);
  letter-spacing:2px;
}
@media (max-width: 690px){
  .nav{
    z-index: 9999;
    top:0;
    left: 0;
    align-items: end;
    width: 100vw;
    position: fixed;
    background:#FFFFF0;
  }
  .nav a{
    font-size:8px;
    text-align: right;
    padding:8px 8px;
  }
}